/* General Styling */
.app {
  text-align: center;
  font-family: Arial, sans-serif;
  margin: 20px;
}

h1 {
  color: #ff69b4;
  margin-bottom: 20px;
  font-size: 2.5rem;
}

/* Marquee Container */
.marquee-container {
  width: 100%;
  overflow: hidden; /* Hide content outside the box */
  border: 2px solid #ff69b4;
  border-radius: 15px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  position: relative;
  margin: 0 auto;
  height: 320px; /* Set the height of the marquee box */
}

/* Marquee Content */
.marquee-content {
  display: flex;
  gap: 15px;
  animation: scroll 20s linear infinite; /* Smooth scrolling effect */
}

/* Photo Box */
.photo-box {
  width: 300px; /* Fixed width for each photo box */
  height: 300px; /* Fixed height for each photo box */
  flex-shrink: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #fff; /* Background for the photo boxes */
  border-radius: 10px;
  padding: 5px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

/* Image Styling */
.photo {
  width: 100%; /* Fill the width of the box */
  height: 100%; /* Fill the height of the box */
  object-fit: cover; /* Ensures the image covers the box while maintaining aspect ratio */
  border-radius: 10px;
}

/* Keyframe Animation for Scrolling */
@keyframes scroll {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-100%);
  }
}

/* Mobile Responsiveness */
@media (max-width: 600px) {
  h1 {
    font-size: 1.8rem;
  }

  .photo-box {
    width: 250px;
    height: 250px;
  }
}
